.confirm--button--component {
    width: 100%;
    max-width: 80px;
    background-color: rgb(62, 62, 62);
    color: white;
    height: 36px;
    font-size: 13px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 0.5rem;

    @media (min-width: 768px) {
        max-width: 100px;
        height: 44px;
        font-size: 14px;
    }
}
