section#join {
    padding-top: 120px;

    @media (min-width: 768px) {
        padding-top: 300px;
    }

    article.join--container {
        width: 100%;
        max-width: $defaultMaxWidth;
        margin: 0 auto;
        padding: 0 1rem;
        display: flex;
        flex-wrap: wrap;

        .info--wrap {
            width: 100%;

            @media (min-width: 1024px) {
                width: 50%;
            }

            .box {
                max-width: 400px;
                margin: 0 auto;

                h4 {
                    text-align: center;
                    font-weight: bold;
                    margin-bottom: 3rem;
                }

                form {
                    .group {
                        display: flex;
                        margin-bottom: 1.25rem;

                        @media (min-width: 768px) {
                            margin-bottom: 1.5rem;
                        }

                        &.wrap {
                            flex-wrap: wrap;
                        }

                        &.direction-column {
                            flex-direction: column;
                        }

                        &.align-start {
                            align-items: flex-start;
                        }

                        &.margin--top-64px {
                            @media (min-width: 1024px) {
                                margin-top: 64px !important;
                            }
                        }

                        & > .group {
                            margin-bottom: 0;
                        }

                        label.number--box {
                            margin-left: 1.5%;
                        }

                        .line {
                            width: 100%;
                            height: 10px;
                            background-color: rgb(62, 62, 62);
                            margin-bottom: 0.7rem;

                            & ~ label {
                                margin-bottom: 0.7rem;
                            }

                            & ~ span {
                                font-size: 10px;
                                color: rgba(0, 0, 0, 0.5);

                                @media (min-width: 768px) {
                                    font-size: 11px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
